var test = require('ava');
var app = require('../app');
var request = require('supertest-promised')(app);

test.serial('fail open', function () {
  return request.post('/')
    .send({
      "host": "1.1.1.999",
      "user": "admin",
      "pass": "admin"
    })
    .expect(400);
});

test.serial('success open', function (t) {
  return request.post('/')
    .send({
      "host": "197.211.242.121",
      "user": "admin",
      "pass": "admin"
    })
    .expect(200)
    .expect(function (res) {
      t.is(res.body.status, 'Connection success');
    });
});

test.serial('success close', function (t) {
  return request.post('/')
    .send({
      host: "197.211.242.121",
      close: 1
    })
    .expect(200)
    .expect(function (res) {
      console.log(res.body);
      t.is(res.body.status, 'Connection closed');
    });
});

test.serial('close not opened', function (t) {
  return request.post('/')
    .send({
      host: "197.211.1.1",
      close: 1
    })
    .expect(400);
});
